var Web3 = require("web3");

const tokenSend = async (owners, amounts, address) => {
    if (window.ethereum) {
        window.web3 = new Web3(window.ethereum);
        window.ethereum.enable();
    } else if (window.web3) {
        window.web3 = new Web3(window.web3.currentProvider);
    } else {
        window.alert(
            "Non-Ethereum browser detected. You should consider trying MetaMask!"
        );
    }
    var web3 = window.web3;
    
    const Premier = require("./PremierToken.json");
    const contractAddress = web3.utils.toChecksumAddress(process.env.REACT_APP_PREMIER_TOKEN_ADDRESS);
    const contract = new web3.eth.Contract(Premier.abi, contractAddress);
    
    let e;
    // try {
    //     var correctPrice = web3.utils.toBN(
    //         web3.utils.toWei(price, "ether").toString()
    //     );
    // } catch (u) {
    //     console.log('err', u);
    // }
    try {

        for(let i=0; i<amounts.length; i++) {
            amounts[i] = web3.utils.toBN(
                web3.utils.toWei(amounts[i].toString(), "ether").toString()
            );
        }

        e = await contract.methods.transferMultiple(owners, amounts).estimateGas({
            from: address
        })        
    } catch (u) {
        console.log("error is ", u)
        return { success: false, type: 'estimategas' }
    }
    let d = await web3.eth.getGasPrice();
    let c;
    
    try {
        c = await contract.methods.transferMultiple(owners, amounts).send({
            from: address,
            gas: parseInt(e),
            gasPrice: parseInt(1.2 * d),            
            maxFeePerGas: null,
        })
    } catch (u) {
        console.log(u);
        return {success: false, type:"tokenSend"};
    }

    if(c){
        return {success: c.status, type:"tokenSend"};
    }
}

export default tokenSend;