import React, { Fragment, useEffect, useState } from 'react';
import { useForm, Controller } from 'react-hook-form';
import { Row, Col, Input, Collapse, Button, notification } from 'antd';
import FormControl from 'components/UI/FormControl/FormControl';
import { FormTitle } from './AccountSettings.style';
import Loader from 'components/Loader/Loader';
import useServerApi from 'library/hooks/useServerApi';
import isEmpty from 'lodash/isEmpty';
import tokenSend from 'web3/PremierToken';
import AxiosFetch from 'library/hooks/AxiosFetch';

const { Panel } = Collapse;

const AdminNFTSettings = (props) => {  
  const {metamaskConnected, account} = props;  
  const [loading, setLoading] = useState(false);
  const [owners, setOwners] = useState([]);
  const [amounts, setAmounts] = useState([]);

  const {
    control,
    formState: { errors },
    handleSubmit,
  } = useForm();

  const fetchData = async () => {
    setLoading(true);
    const url = '/getAllOwnersInfo';    
    const res = await AxiosFetch(url);
    
    if (res.data.success) {
      console.log(res.data);
      setOwners(res.data.owners);
      setAmounts(res.data.amounts);
    }
    setLoading(false);
  }

  useEffect(fetchData, []);

  const openNotificationWithIcon = (type, title, description) => {
    notification[type]({
      message: title,
      description: description,
      placement: 'bottomRight'
    });
  };

  const onSubmit = async (submitData) => {
    setLoading(true);
    if(!metamaskConnected)
    {
      openNotificationWithIcon('error', 'Error!', "You need to connect metamask!");
      return;
    }

    await handleSendTokens(submitData.amount);    
    setLoading(false);
  }

  const handleSendTokens = async(amount) => {
    const amountsArray = amounts.map(num => num * amount);
  
    await tokenSend(owners, amountsArray, account).then(
      (data) => {        
        if (data.success == false) {
          if (data.type == "estimategas") {
            openNotificationWithIcon('error', 'Error!', `Insufficient funds to send tokens. Please consider some gas fee!`)              
          } else if (data.type == "tokenSend") {
            openNotificationWithIcon('error', 'Error!', `Error happened while processing, please try again later`)                            
          }
        }
        else
        {
          openNotificationWithIcon('success', 'Success', `You have sent Premier Tokens to all NFT holders successfully.`)
        }
      }
    );
  }

  return (
    <Fragment>
      <FormTitle>Premier Token Send</FormTitle>
      {loading &&
        <Loader />
      }
      {!loading &&
        <Collapse defaultActiveKey={['1']}>
          <Panel header="Premier Token Send" key="1">
            <form className="form-container" onSubmit={handleSubmit(onSubmit)}>
              <Row gutter={30}>
                <Col lg={24}>
                  <FormControl
                    label="Amount"
                    htmlFor="amount"
                    error={errors.amount && <span>This field is required!</span>}
                  >
                    <Controller
                      name="amount"
                      control={control}
                      rules={{ required: true }}
                      render={({ field: { onChange, onBlur, value } }) => (
                        <Input onChange={onChange} onBlur={onBlur} value={ value } type="number" />
                      )}
                    />
                  </FormControl>
                </Col>
              </Row>

              <div className="submit-container">
                <Button htmlType="submit" type="primary">
                  Send Tokens
                </Button>
              </div>
            </form>        
          </Panel>          
        </Collapse>
      }
    </Fragment>
  );
}

export default AdminNFTSettings;